import React from "react";
import SeparatedTemplate29 from "../Templates/SeparatedTemplate29";
import classNames from "classnames";
import END from "../../assets/images/29p_images/29 The end.png";
import AND from "../../assets/images/29p_images/29 And.png";
import PrintIcon from "../../assets/icons/29-printer-7575.svg";
import HomeIcon from "../../assets/icons/29-home7575.svg";

import "../../scss/pages.scss";

function P29() {
  const translateFlower = (f) => {
    let fInKorean = "";
    if (f === "rose") fInKorean = "";
    if (f === "freesia") fInKorean = "";
    if (f === "babyBreath") fInKorean = "";
    if (f === "tulip") fInKorean = "";
    if (f === "mum") fInKorean = "";
    return fInKorean;
  };

  const makeRestText = (choice) => {
    let restText = "";
    const travelToAlone = localStorage.getItem("travelToAlone") ?? "";
    const travelWithTo = localStorage.getItem("travelWithTo") ?? "";
    const travelWithWho = localStorage.getItem("travelWithWho") ?? "";
    const homeDoAlone = localStorage.getItem("homeDoAlone") ?? "";
    const homeWithWho = localStorage.getItem("homeWithWho") ?? "";
    const homeWithDo = localStorage.getItem("homeWithDo") ?? "";

    if (choice === "travel_alone")
      restText =
        travelToAlone + "()     ,       ?          .";

    if (choice === "travel_together")
      restText =
        travelWithTo +
        "() " +
        travelWithWho +
        "()     ,       ?          .";

    if (choice === "home_alone")
      restText =
        "   " +
        homeDoAlone +
        "  ,          ?              .";

    if (choice === "home_together")
      restText =
        homeWithWho +
        "()    " +
        homeWithDo +
        "  ,          ?           .";

    return restText;
  };

  const name = localStorage.getItem("name") ?? "";
  const selfie = JSON.parse(localStorage.getItem("photo"));
  const flower = localStorage.getItem("flower") ?? "rose";
  const travelOrHome = localStorage.getItem("choice_p8").startsWith("travel") ? "travel" : "home";
  const deathMonthNDay = localStorage.getItem("deathMonth") + " " + localStorage.getItem("deathday") + "";
  const alias = localStorage.getItem("alias") ?? "   ";
  const lastWord = localStorage.getItem("lastWord") ?? "   ";
  const flowerInKorean = translateFlower(flower);
  const happyMoment = localStorage.getItem("travelHappyMoment") ?? localStorage.getItem("homeHappyMoment") ?? "  ";
  const sadMan = localStorage.getItem("sadMan") ?? "";
  const restText = makeRestText(localStorage.getItem("choice_p8"));
  const to = JSON.parse(localStorage.getItem("messages"))
    .map((e) => e.to)
    .join(", ");

  const data = {
    deco_data: {
      selfie: selfie,
      flower: flower,
      travelOrHome: travelOrHome,
    },
    title_data: {
      writer: name + "",
      lastPage: "",
    },
    l_paras_data: {
      img: END,
      paragraph1: {
        header: name + " ",
        body: (
          <>
            {name} {deathMonthNDay}  .
            <br />
            {name}       {name} . {alias}  {name} {lastWord}   .
          </>
        ),
      },
      paragraph2: {
        header: name + " ",
        body: name + "          .    " + flowerInKorean + "   .",
      },
      paragraph3: {
        header: name + "  ",
        body: (
          <>
            {name} {happyMoment}  .
            <br /> , {name}     .
          </>
        ),
      },
    },
    r_paras_data: {
      img: AND,
      paragraph1: {
        header: "  ",
        body:
          sadMan +
          "    .       .        .   " +
          sadMan +
          "        ?",
      },
      paragraph2: {
        header: " ",
        body: restText,
      },
      paragraph3: {
        header: "  ",
        body:
          "  " +
          to +
          "  .         .             .       .",
      },
    },
    detailed_buttons_data: {
      print_button_data: {
        icon: {
          alt: "print-icon",
          src: PrintIcon,
        },
        span_data: "",
      },
      home_button_data: {
        icon: {
          alt: "home-icon",
          src: HomeIcon,
        },
        span_data: "",
      },
    },
  };

  const styleName = {
    tp_style: [],
    title_style: {
      writer_style: ["title", "writer"],
      lastPage_style: ["title", "lastPage"],
    },
    l_paras_style: {
      img_style: "end",
      para1_style: {
        head_style: ["para", "head"],
        body_style: ["para", "body", "h3"],
      },
      para2_style: {
        head_style: ["para", "head"],
        body_style: ["para", "body", "h1"],
      },
      para3_style: {
        head_style: ["para", "head"],
        body_style: ["para", "body", "h1"],
      },
    },
    r_paras_style: {
      img_style: "and",
      para1_style: {
        head_style: ["para", "head"],
        body_style: ["para", "body", "h3"],
      },
      para2_style: {
        head_style: ["para", "head"],
        body_style: ["para", "body", "h2"],
      },
      para3_style: {
        head_style: ["para", "head"],
        body_style: ["para", "body", "h2"],
      },
    },
    detailed_buttons_style: {
      print_button_style: {
        button_style: ["circle", "print"],
        icon_style: ["print"],
        span_style: ["nanum", "extra-bold", "print"],
      },
      home_button_style: {
        button_style: ["circle", "home"],
        icon_style: ["home"],
        span_style: ["nanum", "extra-bold", "home"],
      },
    },
  };

  return (
    <div className={classNames("Page", "P29")}>
      <SeparatedTemplate29 data={data} styleName={styleName}></SeparatedTemplate29>
    </div>
  );
}

export default P29;
